import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LandingComponent } from './landing/landing.component';
import { E3empowerComponent } from './e3empower.component';
import { NotfoundComponent } from './notfound/notfound.component';

const e3empowerRoutes: Routes = [  
    { path: '', component: E3empowerComponent, children: [
        { path: '', component: LandingComponent },
        { path: '**', component: NotfoundComponent }
    ] }
];

@NgModule({
  imports: [RouterModule.forChild(e3empowerRoutes)],
  exports: [RouterModule]
})
export class E3empowerRoutingModule { }
