import { Component, OnInit } from '@angular/core';
import { ImageService } from './common/services/image.service';
import { Imagepath } from 'src/assets/images/imagepath';

@Component({
  selector: 'app-e3empower',
  templateUrl: './e3empower.component.html',
  styleUrls: ['./e3empower.component.scss']
})
export class E3empowerComponent implements OnInit {

  imagepaths: Imagepath[];

  constructor(private _imagepath: ImageService) { }

  ngOnInit() {
    this.getImagepath();
  }

  getImagepath(): void {
    this._imagepath.getImagepath().subscribe(imagepaths => this.imagepaths = imagepaths);
    console.log("image path has loaded");
  }

  getImage(id: number): string {
    let path = this.imagepaths.find(imagepath => imagepath.id == id).path;
    return 'image/' + path;
  }
}
